<template>
  <div>
    <v-app-bar
      app
      color="white"
      light
      shrink-on-scroll
      prominent
      fade-img-on-scroll
    >
      <v-app-bar-nav-icon
        class="d-flex d-md-none mx-2"
        @click.stop="mobileBar = !mobileBar"
      ></v-app-bar-nav-icon>

      <div class="d-flex mx-8 mx-sm-auto align-center">
        <router-link :to="{ name: 'Home' }">
          <v-img
            alt="Vuetify Logo"
            class="logo-img shrink ml-lg-12"
            contain
            src="@/assets/yogo3.png"
            transition="scale-transition"
            width="190"
          />
        </router-link>
      </div>

      <div class="d-none d-md-flex mx-auto">
        <v-menu
          v-for="(item, index) in btn"
          :key="item.name"
          open-on-hover
          offset-y
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              class="mx-lg-4 btn-list"
              text
              light
              x-large
              v-bind="attrs"
              v-on="on"
              plain
            >
              {{ item.title }}
            </v-btn>
          </template>

          <v-list v-show="item.list[0].title != ''" class="text-center">
            <v-list-item
              v-for="(list, n) in item.list"
              :key="list.name"
              @click="goToBranch(index, n)"
            >
              <v-list-item-title>{{ list.title }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>
    </v-app-bar>

    <v-navigation-drawer v-model="mobileBar" app fixed temporary>
      <v-list>
        <v-list-group
          v-for="(item, index) in btn"
          :key="item.name"
          v-model="item.active"
          :prepend-icon="item.action"
          no-action
        >
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </template>

          <v-list-item
            v-for="(list, n) in item.list"
            :key="list.name"
            @click="goToBranch(index, n)"
          >
            <v-list-item-content>
              <v-list-item-title v-text="list.title"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  name: "Bar",
  data: () => ({
    btn: [
      {
        title: "關於我們",
        list: [
          {
            title: "公司介紹",
            link: "Company",
          },
        ],
        link: "Companys",
        active: false,
      },
      {
        title: "服務項目",
        list: [
          {
            title: "博弈網站開發串接平台",
            link: "ServiceInfo",
            branch: "博弈網站開發串接平台",
          },
          {
            title: "LINE投注相關產品",
            link: "ServiceInfo",
            branch: "LINE投注相關產品",
          },
          {
            title: "老虎機、娛樂城開發",
            link: "ServiceInfo",
            branch: "老虎機、娛樂城開發",
          },
        ],
        link: "ServiceInfo",
        active: false,
      },
      {
        title: "產品資訊",
        list: [
          {
            title: "產品介紹",
            link: "ProductList",
          },
        ],
        link: "ProductList",
        active: false,
      },
      {
        title: "產品相簿",
        list: [
          {
            title: "老虎機",
            link: "",
          },
          {
            title: "百家樂",
            link: "",
          },
        ],
        link: "",
        active: false,
      },
      {
        title: "文章資訊",
        list: [
          {
            title: "",
            link: "",
          },
        ],
        link: "",
        active: false,
      },
      {
        title: "聯絡我們",
        list: [
          {
            title: "",
            link: "",
          },
        ],
        link: "",
        active: false,
      },
      {
        title: "影片專區",
        list: [
          {
            title: "",
            link: "",
          },
        ],
        link: "",
        active: false,
      },
    ],
    mobileBar: false,
  }),
  methods: {
    goToBranch(index, n) {
      if (this.btn[index].list[n].branch) {
        this.$router.push({
          name: this.btn[index].list[n].link,
          params: { serviceId: this.btn[index].list[n].branch },
        });
      } else {
        this.$router.push({
          name: this.btn[index].list[n].link,
        });
      }
    },
  },
};
</script>

<style lang="css" scoped>
.v-toolbar--prominent .v-toolbar__content {
  align-items: center !important;
}

@media screen and (max-width: 1100px) {
  .btn-list {
    width: 50px;
  }
}

@media screen and (max-width: 320px) {
  .logo-img {
    width: 80%;
  }
}

@media screen and (max-width: 280px) {
  .logo-img {
    width: 65%;
  }
}
</style>
